import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import {SocketService} from './service/socket.service'
import {DataService} from './service/data.service';
import { SummaryComponent } from './summary/summary.component';
import { ModuleConnectionComponent } from './module-connection/module-connection.component';
import { Cm01SettingComponent } from './cm01-setting/cm01-setting.component'

import {AppRoutingModule} from './app-routing/app-routing.module'

@NgModule({
  declarations: [
    AppComponent,
    SummaryComponent,
    ModuleConnectionComponent,
    Cm01SettingComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule
  ],
  providers: [SocketService , DataService],
  bootstrap: [AppComponent]
})
export class AppModule { }



