import { Component, OnInit } from '@angular/core';
import { SocketService } from '../service/socket.service'
@Component({
  selector: 'app-cm01-setting',
  templateUrl: './cm01-setting.component.html',
  styleUrls: ['./cm01-setting.component.css']
})
export class Cm01SettingComponent implements OnInit {
 	private ecth:number = 0;
 	private phth:number = 0;
 	private ecfeed:number = 0;
 	private phfeed:number = 0;
 	private dt:number = 0;
  constructor(private socket:SocketService) { 
  	this.socket.socket.on('CM01_REQ_SETTING' , (data:any)=>{
  		console.log(data)
  		if(data!=undefined || data!=null){
  			this.ecth = data.ecth;
  			this.phth = data.phth;
  			this.ecfeed= data.ecfeed;
  			this.phfeed = data.phfeed;
  			this.dt = data.dt;
  		}
  	})
  }

  ngOnInit() {

  }

  SaveSetting():void {

  	var data = {
  		'ecth': this.ecth,
  		'phth': this.phth,
  		'ecfeed': this.ecfeed,
  		'phfeed': this.phfeed,
  		'dt':this.dt
  	}

  	console.log(data)
  	this.socket.socket.emit('CM01_SAVE_SETTING' , data);
  }


  ecthUp(): void {
    this.ecth += 0.1;
    this.ecth.toFixed(2);
  }

  ecthDown(): void{
    this.ecth -=0.1;
    this.ecth.toFixed(2);
  }

  phthUp(): void {
    this.phth += 0.1;
  }
  phthDown(): void{
    this.phth -=0.1;
  }


  ecfeedUp(): void{
    this.ecfeed++;
  }
  ecfeedDown(): void{
    this.ecfeed--;
  }

  phfeedUp(): void{
    this.phfeed++;
  }
  phfeedDown(): void{
    this.phfeed--;
  }

  dtUp():void {
    this.dt++;
  }
  dtDown():void {
    this.dt--;
  }


}



