import { Injectable } from '@angular/core';
import {SocketService} from './socket.service'
@Injectable()
export class DataService {

  private temp:number;
  private humi:number;
  private wt:number;
  private co2:number;
  private ec:number;
  private ph:number;

  private cm01:boolean;
  private cm02:boolean;
  constructor(private socket:SocketService) { 
  	this.socket.socket.on('SENSOR_DATA' , (data:any)=>{
  		this.temp = data['temp']
  		this.humi = data['humi']
  		this.wt = data['wt']
  		this.co2 = data['co2']
  		this.ph = data['ph']
  		this.ec = data['ec']
  	})

  	this.socket.socket.on('CHECK_CONNECTION' , (data:any)=>{
  		this.cm01 = data['cm01']
  		this.cm02 = data['cm02']
  	})
  }


}



